<template>
    <div class="payment-method">
        <div class="payment-c">
            <div class="asset-title">
                <span class="desc"></span>
                <span class="name">{{$t('center.paymentMethod[0]')}}</span>
            </div>
            <p class="payment-title">{{$t('center.paymentMethod[0]')}}</p>
            <p class="payment-detail">{{$t('center.paymentMethod[1]')}}</p>
            <ul class="payment-item">
                <li class="payment-list" v-for="(item, i) in payList" :key="i">
                    <img :src="getImg(item.type)" alt="" class="payment-img">
                    <span class="name">{{ item.tip }}</span>
                    <span class="account">{{ item.number }}</span>
                    <span class="change" @click="edit(item.type)">{{$t('center.paymentMethod[2]')}}</span>
                    <div class="switch-warp">
                        <el-switch
                        active-color="#13ce66"
                        inactive-color="#009dfa"
                        active-value="1"
                        inactive-value="0"
                        v-model="item.status"
                        @change="changeSwitch(item.type, item.status)">
                        </el-switch>
                        <span class="on" v-show="item.status == 1">ON</span>
                        <span class="off" v-show="item.status == 0">OFF</span>
                    </div>
                </li>
            </ul>
            <button class="add-btn" @click="addPayment">{{$t('center.paymentMethod[3]')}}</button>
        </div>
    </div>
</template>

<script>
import { paymentList, payStatus } from '../../../api/c2c'
export default {
    name: 'paymentMethod',
    data() {
        return {
            value2: '',
            payList: []
        }
    },
    created() {
        // 获取用户支付方式
        this.getPayMethodList()
    },
    methods: {
        changeSwitch(type, status) {
            payStatus({ 
                type: type, 
                val: status, 
                account: sessionStorage.getItem('account') }).then(res=> {
                if (res.data.status == 200) {
                    this.$public.msg(this.$t('center.paymentMethod[4]'), 'success', this)
                    return
                } else {
                    this.$public.msg(res.data.msg, 'error', this)
                    return
                }
            })
        },
        getImg(type) {
            if (type == 'backcard') {
                return require("../../../assets/img/frenchImg/bank.png")
            } else if (type == 'wx') {
                return require("../../../assets/img/frenchImg/wechat.png")
            } else if (type == 'alipay') {
                return require("../../../assets/img/frenchImg/alipay.png")
            }
        }, 
        // 获取用户支付方式
        getPayMethodList() {
            paymentList().then(res=> {
                if (res.status == 200) {
                    this.payList = JSON.parse(JSON.stringify(res.data.data))
                }
            })
        },
        edit(type) {
            this.$router.push({ name: 'addPayment', params: { type: type } })
        },
        addPayment() {
            this.$router.push({ name: 'addPayment' })
        }
    }
}
</script>

<style lang=less>
  @import '../../../assets/index.less';
  @import './paymentMethod.less';
</style>


